import Vue from 'vue'

export const syncApiKeys = (state, apiKeys) => {
  Vue.set(state, 'apiKeys', apiKeys);
  //console.log("---ssss-");
  return state;
}

export const syncExchanges = (state, exchanges) => {
  Vue.set(state, 'exchanges', exchanges);
  return state;
}

export const syncDiscordToken = (state, token) => {
  Vue.set(state, 'discordToken', token);
  return state;
}
export const syncTradeSetup = (state, tradeSetup) => {
  Vue.set(state, 'tradeSetup', tradeSetup);
  return state;
}
export const syncTimeFrame = (state, timeFrame) => {
  Vue.set(state, 'timeFrame', timeFrame);
  return state;
}
