<template lang='pug'>
div.contain
  h2 Config
  h4 Available API keys
  p(v-if='!apiKeySets.length')
    em You don't have any API keys yet.
  ul
    li(v-for='exchange in apiKeySets') {{ exchange }} (
      a(href='#', v-on:click.prevent='removeApiKey(exchange)') remove
      | )
  a.btn--primary(href='#', v-if='!addApiToggle', v-on:click.prevent='openAddApi') Add an API key
  template(v-if='addApiToggle')
    .hr
    apiConfigBuilder
  h4 Available TimeFrames And Token
  .grd
  .grd-row
    .grd-row-col-3-6.px1
      div
        .grd-row
          .grd-row-col-3-6
            p Select Time Frame:
          .grd-row-col-3-6.align
            .custom-select.button
              select(v-model='timeFrame')
                option(v-bind:value='1') 1 min
                option(v-bind:value='5') 5 min
                option(v-bind:value='15') 15 min
                option(v-bind:value='30') 30 min
                option(v-bind:value='60') 60 min
      .txt--left
        a.btn--primary(href='#', v-on:click.prevent='changeTimeFrame') Change Time Frame
    .grd-row-col-3-6.px1
      div
        .grd-row
          .grd-row-col-3-6
            p Input Discord Token:
          .grd-row-col-3-6.align
            input(v-model='discordToken')
      .txt--left
        a.btn--primary(href='#', v-on:click.prevent='changeToken') Change Token
  .hr
  h3 Set up Trade
  .grd
  .grd-row
    .grd-row-col-3-6.px1
      div
        .grd-row
          .grd-row-col-3-6
            p Take Profit % :
          .grd-row-col-3-4.align
            input(v-model='takeProfit')
    .grd-row-col-3-6.px1
      div
        .grd-row
          .grd-row-col-3-6
            p Stop Loss % :
          .grd-row-col-3-4.align
            input(v-model='stopLoss')
  .grd-row
    .grd-row-col-3-6.px1
      div
        .grd-row
          .grd-row-col-3-6
            p Ladder Increase $ :
          .grd-row-col-3-4.align
            input(v-model='ladderInc')
    .grd-row-col-3-6.px1
      div
        .grd-row
          .grd-row-col-3-6
            p Ladder Drop Back :
          .grd-row-col-3-4.align
            input(v-model='ladderDrop')
  .grd-row
    .grd-row-col-1-12.px1
      .txt--center
          a.btn--primary(href='#', v-on:click.prevent='changeSetupTrade') Change Set up

</template>

<script>
import apiConfigBuilder from './apiConfigBuilder.vue';
import { post } from '../../tools/ajax';

export default {
  components: {
    apiConfigBuilder
  },
  data: () => {
    return {
      addApiToggle: false,
      timeFrame: 5,
      ladderDrop: 50,
      ladderInc: 10,
      takeProfit: 25,
      stopLoss: 25
    }
  },
  methods: {
    openAddApi: function() {
      this.addApiToggle = true;
    },
    removeApiKey: function(exchange) {
      if(!confirm('Are you sure you want to delete these API keys?'))
        return;

      post('removeApiKey', {exchange}, (error, response) => {
        if(error)
          return alert(error);
      });
    },
    changeTimeFrame: function () {
      console.log(this.timeFrame);
      var timeFrame = this.timeFrame;
      post('changeTimeFrame', {timeFrame}, (error, response) => {
        if(error)
          return alert(error);
      });
    },
    changeToken: function () {
      console.log(this.discordToken);
      var discordToken = this.discordToken;
      post('setDiscordToken', {discordToken}, (error, response) => {
        if(error)
          return alert(error);
      });
    },
    changeSetupTrade: function () {
      console.log(this.takeProfit);
      // var discordToken = this.discordToken;
      post('changeSetupTrade', {takeProfit: this.takeProfit, stopLoss: this.stopLoss, ladderInc: this.ladderInc, ladderDrop: this.ladderDrop}, (error, response) => {
         if(error)
           return alert(error);
       });
    },
  },
  computed: {
    apiKeySets: function() {
      return this.$store.state.apiKeys
    },
    takeProfit: function () {
      return this.$store.state.tradeSetup.takeProfit;
    }
  },
  watch: {
    apiKeySets: function() {
      this.addApiToggle = false;
    }
  }
}
</script>
